import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-add-dashboard-dialog',
  templateUrl: './add-dashboard-dialog.component.html',
  styleUrls: ['./add-dashboard-dialog.component.css']
})
export class AddDashboardDialogComponent {
  type: number;
  nameNewPanel: string;
  param: string;
  resultConfig: any;

  constructor(
    public dialogRef: MatDialogRef<AddDashboardDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {}

  onNoClick(): void {
      this.dialogRef.close();
  }

  onChange () {
    this.resultConfig = {
      nameNewPanel: this.nameNewPanel,
      type: this.type,
      param: this.param
    };
  }
}
