import {Component} from "@angular/core";
import {ICellRendererAngularComp} from "ag-grid-angular";

@Component({
    selector: 'child-cell',
    template: '<a (click)="openTransactionPopup()">{{callid}}</a>'
})
export class ColumnCallidRenderer implements ICellRendererAngularComp {
    public params: any;
    callid: string;

    agInit(params: any): void {
        this.params = params;
        this.callid = this.params.value || null;
    }

    public openTransactionPopup() {
        // const color = this.params.eGridCell.style.color;
        this.params.context.componentParent.openTransactionForSelectedRows(this.params.node.rowIndex, this.params.data);
    }

    refresh(): boolean {
        return false;
    }
}