import {Component} from "@angular/core";
import {ICellRendererAngularComp} from "ag-grid-angular";

@Component({
    selector: 'child-cell',
    template: '<a (click)="openMethodPopup()">{{method}}</a>'
})
export class ColumnMethodRenderer implements ICellRendererAngularComp {
    public params: any;
    method: string;

    agInit(params: any): void {
        this.params = params;
        this.method = this.params.value || null;
    }

    public openMethodPopup() {
        this.params.context.componentParent.openMethodForSelectedRow(this.params.node.rowIndex, this.params.data)
    }

    refresh(): boolean {
        return false;
    }
}