import { Component, OnInit, Input } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { SettingRsearchWidgetComponent } from './setting-rsearch-widget.component';
import { IWidget } from '../IWidget';

@Component({
    selector: 'app-rsearch-widget',
    templateUrl: './rsearch-widget.component.html',
    styleUrls: ['./rsearch-widget.component.css']
})
export class RsearchWidgetComponent implements OnInit, IWidget {
    @Input() id: string;

    constructor(public dialog: MatDialog) { }

    ngOnInit() {
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(SettingRsearchWidgetComponent, {
            width: '90vw',
            data: {name: 'this.name', animal: 'this.animal'}
        });

        const dialogRefSubscription = dialogRef.afterClosed().subscribe(result => {
            dialogRefSubscription.unsubscribe();
        });
    }
}
