import { Component, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormControl } from '@angular/forms';
import { MatTable } from '@angular/material/table';
import { StatisticService } from '../../../services/statistic.service';
import { ChartType } from 'chart.js';
import { AlertService } from '../../../services/alert.service';

export interface Timestamp {
    from: number;
    to: number;
}
export interface PeriodicElement {
    id: string;
    panelDataSource: string;
    database: string;
    retentionPolicy: string;
    buttons: boolean;
    detail?: {
        measurement?: string;
        counter?: Array<string>;
        tags?: Array<string>;
        values?: Array<string>;
        sum?: boolean;
    };
}

export interface SelectList {
    name: string;
    value: string;
}

@Component({
    selector: 'app-setting-influxdbchart-widget-component',
    templateUrl: 'setting-influxdbchart-widget.component.html',
    styleUrls: ['./setting-influxdbchart-widget.component.css']
})

export class SettingInfluxdbchartWidgetComponent {
    @ViewChild(MatTable, {static: true}) table: MatTable<any>;

    displayedColumns: string[] = ['id', 'panelDataSource', 'database', 'retentionPolicy', 'buttons'];
    dataSource: PeriodicElement[] = [];
    detailShow = false;

    chartTypeList = [
        'line', 'bar', 'horizontalBar', 'radar',
        'doughnut', 'polarArea', 'area', 'pie'
    ];

    databaseList: SelectList[] = [];
    retentionPolicyList: SelectList[] = [];
    measurementList: SelectList[] = [];
    counterList: SelectList[] = [];
    tagsList: SelectList[] = [];

    chartTitle: string;
    chartType: string;
    format: string;

    panelDataSource: string;
    database: string;
    retentionPolicy: string;

    measurement: string;
    counter = new FormControl();
    tags = new FormControl();
    values = new FormControl();
    isSum: boolean;
    selecedEditQuery: PeriodicElement;

    apiQueryPath: string;
    apiQueryValue: string;
    curlDebug: string;
    curlResponse: string;

    timestamp: Timestamp = {
        from: 1567555200000,
        to: 1567576262580
    };

    outputObject: any = {};

    constructor(
        private _ss: StatisticService,
        private alertService: AlertService,
        public dialogRef: MatDialogRef<SettingInfluxdbchartWidgetComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any) {
            if (data.empty) {
                return;
            }
            this.chartType = data.chart.type.value;
            this.chartTitle = data.title;
            this.format = data.format.value;
            if (data.panel && data.panel.queries) {
                const queries: Array<any> = data.panel.queries;
                // const queries: Array<any> = data.dataquery.data;
                queries.forEach(item => {
                    const item2 = data.dataquery.data.filter(i =>
                        i.database.name === item.database.name &&
                        i.retention.name === item.retention.name)[0];

                    this.dataSource.push({
                        id: item.name,
                        panelDataSource: item.type.name,
                        database: item.database.name,
                        retentionPolicy: item.retention.name,
                        buttons: true,
                        detail: {
                            measurement: item2.main.name,
                            counter: item2.type.map(i => i.name),
                            tags: item2.tag.map(i => i.name),
                            values: [],
                            sum: item2.sum
                        }
                    });
                });
            }
            this.updateResult();
        }

    onNoClick(): void {
        this.dialogRef.close();
    }

    addRecord() {
        if (this.panelDataSource && this.database && this.retentionPolicy) {
            const row: PeriodicElement = {
                id: `A${this.dataSource.length + 1}`,
                panelDataSource: this.panelDataSource,
                database: this.database,
                retentionPolicy: this.retentionPolicy,
                buttons: true
            };

            this.dataSource.push(row);
            this.table.renderRows();
            this.updateResult();
        } else {
            this.alertService.error('error: need select all');
            setTimeout(() => {
                this.alertService.hide();
            }, 5000);
        }
    }

    editRecord(id: any) {
        this.showDetail();
        this.selecedEditQuery = this.dataSource.filter(item => item.id === id)[0];
        if (!this.selecedEditQuery.detail) {
            this.selecedEditQuery.detail = {
                measurement: '',
                counter: [],
                sum: false,
                tags: [],
                values: []
            };
        } else {
            this.measurement = this.selecedEditQuery.detail.measurement;
            this.isSum = this.selecedEditQuery.detail.sum;
            this.onMeasurement();
            this.values.setValue(this.selecedEditQuery.detail.values);
        }

        const getStatisticMeasurementsSubscription = this._ss.getStatisticMeasurements(this.selecedEditQuery.database).subscribe(res => {
            if (res && res.data) {
                this.measurementList = res.data;
            }
            getStatisticMeasurementsSubscription.unsubscribe();
        });
    }

    deleteRecord(id: any) {
        this.dataSource = this.dataSource.filter(item => item.id !== id);
        this.table.renderRows();
        this.updateResult();
    }

    showDetail() {
        this.detailShow = true;
    }

    onPanelDatasource() {
        const getStatisticDbListSubscription = this._ss.getStatisticDbList().subscribe(res => {
            getStatisticDbListSubscription.unsubscribe();
            this.databaseList = res.data;
            this.updateResult();
        });
    }

    onDatabase() {
        const reqData = {
            timestamp: this.timestamp,
            param: {
               search: {
                  main: {
                     name: 'InfluxDB',
                     type: 'JSON',
                     alias: 'influxdb',
                     fields: [
                        {
                           name: 'Main Category',
                           type: 'select-main',
                           value: 'main',
                           data: [
                              {
                                 name: 'ASR NER',
                                 value: 'asr_ner'
                              }
                           ]
                        },
                        {
                           Name: 'Counter',
                           Type: 'select2',
                           Value: 'type',
                           Data: [
                              {
                                 name: 'Total PPS',
                                 value: 'total_pps'
                              },
                              {
                                 name: 'Other PPS',
                                 value: 'other_pps'
                              },
                              {
                                 name: 'Regs PPS',
                                 value: 'regs_pps'
                              },
                              {
                                 name: 'CALLS PPS',
                                 value: 'calls_pps'
                              },
                              {
                                 name: 'Canceled',
                                 value: 'canceled'
                              },
                              {
                                 name: 'failedsessetup',
                                 value: 'failedsessetup'
                              },
                              {
                                 name: 'unauth',
                                 value: 'unauth'
                              },
                              {
                                 name: 'finished',
                                 value: 'finished'
                              }
                           ]
                        },
                        {
                           name: 'Tag',
                           type: 'selecttag',
                           value: 'typetag',
                           data: [
                              {
                                 name: 'User Group',
                                 value: 'tag1'
                              },
                              {
                                 name: 'X-Group',
                                 value: 'tag2'
                              },
                              {
                                 name: 'IP Group IN',
                                 value: 'tag3'
                              },
                              {
                                 name: 'IP Group OUT',
                                 value: 'tag4'
                              },
                              {
                                 name: 'Tag 5',
                                 value: 'tag5'
                              }
                           ]
                        },
                        {
                           name: 'Group',
                           type: 'select3',
                           value: 'tag',
                           data: [
                              {
                                 name: 'Total PPS',
                                 value: 'total_pps'
                              },
                              {
                                 name: 'Other PPS',
                                 value: 'other_pps'
                              },
                              {
                                 name: 'Regs PPS',
                                 value: 'regs_pps'
                              },
                              {
                                 name: 'CALLS PPS',
                                 value: 'calls_pps'
                              },
                              {
                                 name: 'Canceled',
                                 value: 'canceled'
                              },
                              {
                                 name: 'failedsessetup',
                                 value: 'failedsessetup'
                              },
                              {
                                 name: 'unauth',
                                 value: 'unauth'
                              },
                              {
                                 name: 'finished',
                                 value: 'finished'
                              }
                           ]
                        },
                        {
                           name: 'Sum',
                           type: 'checkbox',
                           value: 'sum'
                        },
                        {
                           name: 'Limit',
                           type: 'input',
                           value: 'limit'
                        }
                     ],
                     settings:  {
                        path:  'statistic/data',
                        query:  JSON.stringify({
                            timestamp: {
                                from: '@from_ts',
                                to: '@to_ts'
                            },
                            param: {
                                filter: [ '@filters' ],
                                limit: '@limit',
                                total: '@total'
                            }
                        }),
                        method:  'GET',
                        limit:  500,
                        total:  false,
                        eval: {
                            incoming: {
                                name: 'test incoming',
                                value: 'var object = @incoming; return object'
                            }
                        },
                        timefields: [
                           {
                              field: 'from_ts',
                              desc: 'From Timestamp'
                           },
                           {
                              field: 'to_ts',
                              desc: 'To Timestamp'
                           }
                        ],
                        fieldvalues: [
                           {
                              field: 'total',
                              desc: 'All Packets'
                           }
                        ],
                        filters: [
                           {
                              type: 'type',
                              desc: 'Data Statistics',
                              options: [
                                 {
                                    value: 'asr',
                                    desc: 'Answer Seizure Ratio'
                                 },
                                 {
                                    value: 'ner',
                                    desc: 'Network Effectiveness Ratio'
                                 },
                                 {
                                    value: 'packet_size',
                                    desc: 'Packet Size'
                                 },
                                 {
                                    value: 'packet_count',
                                    desc: 'Packet Count'
                                 },
                                 {
                                    value: 'sdf',
                                    desc: 'counter of call’s releases except busy and normal call clearing (17 && 16)'
                                 },
                                 {
                                    value: 'isa',
                                    desc: 'counter of replies on INVITE: 408|50[03]'
                                 },
                                 {
                                    value: 'sd',
                                    desc: 'counter of replies on INVITE: 50[034]'
                                 },
                                 {
                                    value: 'ssr',
                                    desc: 'all success setup rate'
                                 }
                              ]
                           }
                        ]
                     }
                  },
                  database: this.database
               },
               limit: 100,
               total: false
            }
        };

        const getStatisticRetentionsSubscription = this._ss.getStatisticRetentions(reqData).subscribe(res => {
            getStatisticRetentionsSubscription.unsubscribe();
            this.retentionPolicyList = res.data;
            this.retentionPolicyList.push({ name: 'none', value: 'none'});

            this.updateResult();
        });
    }

    onChartType() {
        this.updateResult();
    }

    onMeasurement() {
        const reqData = {
            timestamp: this.timestamp,
            param: {
                search: {},
                limit: 100,
                total: true,
                query: [{
                    main: this.measurement,
                    database: this.selecedEditQuery.database,
                    retention: this.selecedEditQuery.retentionPolicy
                }]
            }
        };

        const reqDataTags = {
            timestamp: this.timestamp,
            param: {
                search: {},
                limit: 100
            }
        };

        const getStatisticMetricsSubscription = this._ss.getStatisticMetrics(reqData).subscribe(res => {
            getStatisticMetricsSubscription.unsubscribe();
            if (res && res.data) {
                this.counterList = res.data;
                this.counter.setValue(this.selecedEditQuery.detail.counter);
                this.selecedEditQuery.detail.measurement = this.measurement;
                this.updateResult();
            }
        });
        const getStatisticTagsSubscription = this._ss.getStatisticTags(reqDataTags).subscribe(res => {
            getStatisticTagsSubscription.unsubscribe();
            if (res && res.data) {
                this.tagsList = res.data;
                this.tags.setValue(this.selecedEditQuery.detail.tags);
                this.updateResult();
            }
        });

        this.updateResult();
    }

    onCounter () {
        this.selecedEditQuery.detail.counter = this.counter.value;
        this.updateResult();
    }

    onTag() {
        this.selecedEditQuery.detail.tags = this.tags.value;
        this.updateResult();
    }

    onValues() {
        this.selecedEditQuery.detail.values = this.values.value;
        this.updateResult();
    }

    onSum() {
        this.selecedEditQuery.detail.sum = this.isSum;
        this.updateResult();
    }

    updateResult () {
        if (this.selecedEditQuery) {
            this.dataSource[this.dataSource.map(i => i.id).indexOf(this.selecedEditQuery.id)] = this.selecedEditQuery;
        }
        this.outputObject.chartType = this.chartType;
        this.outputObject.chartTitle = this.chartTitle;
        this.outputObject.format = this.format;
        this.outputObject.panelDataSource = this.panelDataSource;
        this.outputObject.dataSource = this.dataSource;
    }
}
