import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Functions } from '../../../helpers/functions';

@Component({
    selector: 'app-detail-dialog',
    templateUrl: './detail-dialog.component.html',
    styleUrls: ['./detail-dialog.component.css']
})
export class DetailDialogComponent implements OnInit {
    @Input() titleId: string;
    @Input() sipDataItem: any;
    @Input() headerColor: any;

    _isLoaded = false;
    get isLoaded(): boolean {
        return this._isLoaded;
    }
    @Input('isLoaded') set isLoaded(val) {
        this._isLoaded = val;
        if (this.sipDataItem) {
            this.dataLogs = this.sipDataItem.data.messages.filter(i => !i.method).map(i => ({ payload: i }));
        }
    }

    @Output() close: EventEmitter<any> = new EventEmitter();

    dataLogs: Array<any>;
    arrMessageDetail: Array<any> = [];

    constructor() { }

    ngOnInit () {
        if (this.sipDataItem) {
            this.dataLogs = this.sipDataItem.data.messages.filter(i => !i.method).map(i => ({ payload: i }));
        }
    }

    onClose () {
        this.close.emit();
    }

    addWindow(data: any) {
        data.messageDetaiTableData = Object.keys(data).map(i => ({name: i, value: data[i]})).filter(i => typeof i.value !== 'object');
        data.headerColor = '#' + Functions.getColorByString(data.method);
        this.arrMessageDetail.push(data);
    }

    closeWindow(id: number) {
        this.arrMessageDetail.splice(id, 1);
    }
}
