import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SettingClockWidgetComponent } from './setting-clock-widget.component';
import { MatDialog } from '@angular/material/dialog';
import { IWidget } from '../IWidget';
import * as moment from 'moment';

export interface TimeZone {
    value: number;
    offset: string;
    name: string;
    desc: string;
}

export interface ClockConfig {
    id?: string;
    datePattern: string;
    showseconds: boolean;
    timePattern: string;
    title: string;
    location: TimeZone;
}

@Component({
    selector: 'app-clock-widget',
    templateUrl: './clock-widget.component.html',
    styleUrls: ['./clock-widget.component.css']
})
export class ClockWidgetComponent implements OnInit, IWidget {
    @Input() config: ClockConfig;
    @Input() index: string;
    @Input() id: string;

    @Output() change = new EventEmitter<any> ();

    desc: string;
    name: string;
    objDate: Date;
    _config: ClockConfig;
    location_value: number;
    constructor(public dialog: MatDialog) { }

    ngOnInit() {
        this._config = {
            id: this.id,
            datePattern: 'YYYY-MM-DD',
            location: {
                desc: 'Moscow Standard Time',
                name: 'GMT+3 MSK',
                offset: '+3',
                value: -180,
            },
            showseconds: false,
            timePattern: 'HH:mm:ss',
            title: 'Home Clock'
        };

        if (this.config) {
            this._config.title = this.config.title || 'Clock Widget';
            this._config.datePattern = this.config.datePattern || 'YYYY-MM-DD';
            this._config.showseconds = this.config.showseconds || false;
            this._config.timePattern = this.config.timePattern || 'HH:mm:ss';

            if (this.config.location) {
                this._config.location.desc = this.config.location.desc || 'Greenwich Mean Time';
                this._config.location.name = this.config.location.name || 'GMT+0 UTC';
                this._config.location.offset = this.config.location.offset || '+0';
                this._config.location.value = this.config.location.value || 0;
            }
        }

        this.desc = this._config.location.desc;
        this.name = this._config.location.name;
        this.location_value = this._config.location.value;
        const _interval = setInterval(() => {
            this.objDate = moment().add(this.location_value, 'hours').toDate();
        }, 1000);
        this.objDate = moment().add(this.location_value, 'hours').toDate();
    }
    openDialog(): void {
        const dialogRef = this.dialog.open(SettingClockWidgetComponent, {
            width: '250px',
            data: {
                name: this.name,
                desc: this.desc,
                title: this._config.title,
                location: this._config.location
            }
        });

        const dialogRefSubscription = dialogRef.afterClosed().subscribe( (data: any) => {
            if (data) {
                this._config.location = data.location;

                this.name = data.name;
                this.desc = data.desc;
                this._config.title = data.title;
                
                this.change.emit(this._config);
            }
            dialogRefSubscription.unsubscribe();
        });
    }

}
