import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import 'brace';
import 'brace/mode/text';
import 'brace/theme/github';

@Component({
  selector: 'app-dialog-mapping',
  templateUrl: './dialog-mapping.component.html',
  styleUrls: ['./dialog-mapping.component.css']
})
export class DialogMappingComponent {
  constructor(
    public dialogRef: MatDialogRef<DialogMappingComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      if (data.isnew) {
        data.data = {
          partid: '',
          hep_alias: '',
          hepid: '',
          profile: '',
          partition_step: '',
          retention: '',
          correlation_mapping: {},
          fields_mapping: {}
        };
      }
      data.data.correlation_mapping = data.isnew ? '{}' : JSON.stringify(data.data.correlation_mapping, null, 4);
      data.data.fields_mapping = data.isnew ? '[]' : JSON.stringify(data.data.fields_mapping, null, 4);
    }

  onNoClick(): void {
      this.dialogRef.close();
  }
}
