import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { SettingProtosearchWidgetComponent } from './setting-protosearch-widget.component';
import { Router } from '@angular/router';
// import { SearchProtoService } from '../../../services/search/proto.service';
import { PreferenceMappingProtocolService } from '../../../services/preferences/mapping-protocol.service';
import { IWidget } from '../IWidget';
import { SessionStorageService, UserSettings } from '../../../services/session-storage.service';
import { Md5 } from 'ts-md5/dist/md5';
import { Subscription } from 'rxjs';


export interface SearchConfig {
    title: string;
    isButton: boolean;
    proto: Array<any>;
    profile: Array<any>;
}
export interface SearchFieldItem {
    field_name: string;
    form_type: string;
    hepid: number;
    name: string;
    profile: string;
    selection: string;
    type: string;
    value?: string;
}
@Component({
    selector: 'app-protosearch-widget',
    templateUrl: './protosearch-widget.component.html',
    styleUrls: ['./protosearch-widget.component.css']
})
export class ProtosearchWidgetComponent implements OnInit, OnDestroy ,IWidget {
    @Input() id: string;
    @Input() config: any;
    @Output() change = new EventEmitter<any> ();

    private subscriptionStorage: Subscription;

    buttonState = 'show';
    searchQuery: any;
    fields = [];
    widgetId: string;

    constructor(
        public dialog: MatDialog,
        private router: Router,
        private _sss: SessionStorageService,
        private preferenceMappingProtocolService: PreferenceMappingProtocolService) {}

    ngOnInit() {
        if (!this.config) {
            this.config = {
                id: this.id,
                title: 'Proto Search',
                group: 'Search',
                name: 'protosearch',
                description: 'Display Search Form component',
                refresh: false,
                sizeX: 2,
                sizeY: 2,
                config: {
                   title: 'CALL SIP SEARCH',
                   searchbutton: false,
                   protocol_id: {
                      name: 'SIP',
                      value: 1
                   },
                   protocol_profile: {
                      name: 'call',
                      value: 'call'
                   }
                },
                uuid: 'ed426bd0-ff21-40f7-8852-58700abc3762',
                fields: [],
                row: 0,
                col: 1,
                cols: 2,
                rows: 2,
                x: 0,
                y: 1
            };
        }

        this.widgetId = '_' + Md5.hashStr(JSON.stringify(this.config));

        this.config.config = this.config.config || {};
        this.config.fields = (this.config.fields || []).map(item => {
            item.value = '';
            return item;
        });
        // this.fields = this.config.fields;

        this.updateButtonState();

        this.subscriptionStorage = this._sss.sessionStorage.subscribe((data: UserSettings) => {
            const _cache = data.protosearchSettings[this.widgetId]
            if (_cache && _cache.fields) {
                this.fields.forEach(item => {
                    item.value = (_cache.fields.filter(i => i.name === item.field_name)[0] || {value: ''}).value;
                });
            }
        });
    }
    private updateButtonState() {
        this.buttonState = this.config.config.searchbutton ? 'show' : 'hide';
        this.fields = this.config.fields;
    }

    private saveState() {
        this.searchQuery = {
            fields: this.fields.filter((item: any) => item.value !== '')
                .map((item: any) => ({
                    name: item.field_name,
                    value: item.value,
                    type: item.type,
                    hepid: item.hepid
                })),
            protocol_id: '1_' + this.config.config.protocol_profile.value // 1_call | 1_ default | 1_registration
        };
        this._sss.saveProtoSearchConfig(this.widgetId, this.searchQuery);
    }

    onClearFields () {
        this.fields.forEach(item => item.value = '');
        this._sss.removeProtoSearchConfig(this.widgetId);
    }

    openDialog(): void {
        const subscription = this.preferenceMappingProtocolService.getAll().subscribe(data => {
            const dialogRef = this.dialog.open(SettingProtosearchWidgetComponent, {
                width: '600px',
                data: {
                    config: this.config,
                    mapping: data,
                    isButton: this.buttonState === 'show'
                }
            });

            const dialogRefSubscription = dialogRef.afterClosed().subscribe(result => {
                if (result) {
                    if (result.fields && result.fields.length !== 0) {
                        this.config.protocol_id = result.protocol_id;
                        this.config.config.protocol_profile = {
                            name: result.profile,
                            value: result.profile,
                        };
                        this.config.fields = result.fields.map(item => {
                            const res: SearchFieldItem = {
                                field_name: item.id,
                                form_type: item.proto.hep_alias,
                                hepid: 1,
                                name: `1:${result.profile}:${item.id}`,
                                profile: item.proto.profile,
                                selection: item.name,
                                type: 'string',
                            };
                            return res;
                        });
                    }
                    this.config.title = result.title;
                    this.config.config.title = result.title;
                    this.config.config.searchbutton = !!result.isButton;

                    this._sss.removeProtoSearchConfig(this.widgetId);
                    this.widgetId = '_' + Md5.hashStr(JSON.stringify(this.config));

                    this.updateButtonState();
                    this.change.emit(this.config);
                }
                dialogRefSubscription.unsubscribe();
            });

            subscription.unsubscribe();
        });
    }

    onChangeField (event) {
        this.saveState();
    }

    doSearchResult() {
        this.saveState();

        localStorage.setItem('searchQuery', JSON.stringify(this.searchQuery));

        this.router.navigate(['call/result']);
    }

    ngOnDestroy () {
        this.subscriptionStorage.unsubscribe();
    }
}

