import { Component, OnInit, Input } from '@angular/core';
import { ExportCallService } from '../../../../services/export/call.service';
import { DateTimeRangeService, DateTimeTick } from '../../../../services/data-time-range.service';


@Component({
  selector: 'app-tab-export',
  templateUrl: './tab-export.component.html',
  styleUrls: ['./tab-export.component.css']
})
export class TabExportComponent implements OnInit {
    @Input() callid;
    @Input() id;


    constructor(
        private _ecs: ExportCallService,
        private _dtrs: DateTimeRangeService
    ) { }

    ngOnInit() {
    }

    queryBuilder (dtr: DateTimeTick) {
        const localData = JSON.parse(localStorage.getItem('searchQuery'));
        // const protocol_profile = localData.map(i => i.profile)[0]; // 1_call | 1_default | 1_registration
        const search = {};
        search[localData.protocol_id] = {
            id: this.id,
            callid: [this.callid],
            uuid: []
        }
        return {
            timestamp: {
                from: dtr.range.dates[0].unix() * 1000,
                to: dtr.range.dates[1].unix() * 1000
            },
            param: {
                search: search,
                location: {},
                transaction: {
                    call: localData.protocol_id === '1_call',
                    registration: localData.protocol_id === '1_registration',
                    rest: localData.protocol_id === '1_default'
                },
                id: {},
                timezone: {
                    value: -180,
                    name: 'Local'
                }
            }
        }
    }
    exportPCAP () {
        this._dtrs.castRangeUpdateTimeout.subscribe((dtr: DateTimeTick) => {
            const request = this.queryBuilder(dtr);
            const subscription = this._ecs.postMessagesPcap(request).subscribe((data: any) => {
                subscription.unsubscribe();
                this.saveToFile(data, `export_${this.id}.pcap`);
            });
        });
    }

    exportTEXT () {
        this._dtrs.castRangeUpdateTimeout.subscribe((dtr: DateTimeTick) => {
            const request = this.queryBuilder(dtr);
            const subscription = this._ecs.postMessagesText(request).subscribe((data: any) => {
                subscription.unsubscribe();
                this.saveToFile(data, `export_${this.id}.txt`, 'text/plain;charset=utf-8');
            });
        });
    }

    private saveToFile (data, filename, type = 'application/octet-stream') {
        const file = new Blob([data], {type: type});
        if (window.navigator.msSaveOrOpenBlob) {// IE10+
            window.navigator.msSaveOrOpenBlob(file, filename);
        } else { // Others
            const a = document.createElement('a'),
                url = URL.createObjectURL(file);
            a.href = url;
            a.target = '(file)';
            a.download = filename;
            document.body.appendChild(a);
            a.click();
            setTimeout(function() {
                document.body.removeChild(a);
                window.URL.revokeObjectURL(url);
            }, 0);
        }
    }

}
